export default {
	name: 'DiscoverItem',
	data() {
		return {
			
		}
	},
	props: {
		dataList: Array
	},
	methods: {
		callPhone(e) {
			if (!e.currentTarget.dataset.phone) {
				uni.showToast({
					title: '对方没有留下电话，您可以评论留言来联系他',
					icon: 'none'
				})
				return
			}
			uni.makePhoneCall({
				phoneNumber: e.currentTarget.dataset.phone
			})
		},
		commentClick () {
			
		},
		praiseClick() {
			
		},
		shareClick() {
			
		}
	}
}
